.passwordGenerator-container{
    width: 100%;
    height: 100%;
    margin-top: 10rem;
    display: flex;
    flex-direction: column;
   /* justify-content: center; */
    align-items: center;
}
.passwordGenerator-content {
    padding: 2rem;
    background-color: #4f4f4f;
    border-radius: 10px;
}

.passwordGenerator-header{
    margin-bottom: 2rem;
}

.passwordGenerator-content-top {
    display: flex;
    margin-bottom: 2rem;
}
.custom-form-control{
    color: #009688;
    font-size: 1rem;
    padding: 10px;
    border-radius: 5px 0 0 5px;
    width: 100%;
    height: 3rem;
    outline: none;
    border: none;
}
.custom-btn{
    color: #fff;
    height: 3rem;
    padding: 5px 10px;
    background-color: #009688;
    border: none;
    border-radius: 0 5px 5px 0;
}
/* .passwordGenerator-content-bottom {
   margin-bottom: 2rem; 
} */
.custom-form-control-range{
    margin-right: 1rem;
    cursor: pointer;
    accent-color: #009688;
}
.input-container{
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
}
.custom-form-control-check{
    width: 2rem;
    height: 1.5rem;
    accent-color: #009688;
    margin-right: 1rem;
}

